            p.mb-0(data-aos="fade-up") La codorniz (<em>Coturnix coturnix japonica</em>) se ha consolidado en Colombia como una especie menor de gran relevancia en sistemas alternativos, tanto por su alta eficiencia productiva como por su excelente adaptabilidad a espacios reducidos. Su ciclo corto y su versatilidad permiten obtener carne y huevos con muy buena rentabilidad bajo condiciones de manejo tcnico.
    
